.page {
  display: block;
  width: 95%;
  min-width: 800px;
  max-width: 1920px;
  font-family: 'Arial';
  font-weight: normal;
  font-size: 16px;
  align-self: center;
  padding: 50px; }
  .page__header {
    display: block;
    width: auto;
    margin: 0 auto;
    margin-bottom: 50px;
    font-size: 48px;
    padding: 10px;
    text-align: center;
    background-color: #e5e5e5;
    border-radius: 5px; }
  .page__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center; }
  .page__slider {
    width: 30%;
    display: flex;
    justify-content: center;
    padding: 30px 0px; }
    .page__slider_1st {
      font-size: 24px; }
    .page__slider_2nd {
      font-size: 12px; }
  .page__inputs {
    width: 39%;
    text-align: center; }
  .page__label {
    display: inline-block;
    width: 120px;
    color: gray;
    font-size: 16px;
    text-align: right; }
    .page__label_shot {
      width: 80px; }
  .page__input {
    display: inline-block;
    width: 150px;
    margin: 4px;
    height: 30px;
    border-radius: 5px;
    border: 0px;
    background-color: #e5e5e5;
    color: gray;
    font-size: 14px;
    padding-left: 12px; }
  .page .tick {
    margin: 10px 10px; }
  .page__result {
    display: inline-block;
    width: 100%;
    margin: 4px;
    border-radius: 5px;
    border: 0px;
    background-color: #e5e5e5;
    color: gray;
    font-size: 14px;
    padding-left: 12px; }
